.button {
    margin-bottom: 30px;
    cursor: pointer;
    padding: 15px 50px;
    display: inline-block;
    margin: 15px 30px;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 700;
    color: #fff;
    background: #226fbe;
    border: 4px solid #226fbe;
    transition: all 0.3s;
}

.button:hover {
    background: transparent;
    color: #226fbe;
}